import {
  // BrowserRouter as Router,
  // Route,
  // Routes,
  Link
} from 'react-router-dom';

import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

//import { FontAwesomeIcon } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch, faHeartbeat } from '@fortawesome/fontawesome-free-solid';

import recommended_example_photo from '../../../Assets/Images/recommened-example.jpg';
import icon_check_star from '../../../Assets/Images/icon-check-star-13x13.svg';
import trending_example from '../../../Assets/Images/trending_example.jpg';

export default function HealthSupportGroups(){

  const categories_boxes_links = [
    ['Mental Health', <FontAwesomeIcon icon={faHeartbeat} />, './demo/path', 'danger'],
    ['Child', <FontAwesomeIcon icon={faHeartbeat} />, './demo/path', 'success'],
    ['Heart', <FontAwesomeIcon icon={faHeartbeat} />, './demo/path', 'info'],
    ['Kidney', <FontAwesomeIcon icon={faHeartbeat} />, './demo/path', 'info-2'],
    ['Nutritients', <FontAwesomeIcon icon={faHeartbeat} />, './demo/path', 'warning'],
    ['Lungs', <FontAwesomeIcon icon={faHeartbeat} />, './demo/path', 'danger-2'],
  ].map((link_details, key)=><li key={`category-key-${key}`}>
    <Link to={link_details[2]} className={link_details[3]}>
      <span>
        {link_details[1]}
        <br />
        {link_details[0]}
      </span>
    </Link>
  </li>);




  const recommended_blocks = [
    1,2,3
  ].map((rb_details, key)=><li key={`recommended-block-${key}`}>
    <Link to="">
      <span>
        <span>
          <h4>Office Syndrome <img src={icon_check_star} alt="Office Syndrome" /></h4>
          <span>Health</span>
          <span>4.5M members</span>
        </span>
        <span className="recommended_blocks_img" style={{backgroundImage:`url("${recommended_example_photo}")`}}></span>
      </span>
    </Link>
  </li>);


  const trending_blocks = [
    ['', 'success'],
    ['', 'warning'],
    ['', 'danger'],
    ['', 'info'],
    ['', 'info-2']
  ].map((block_details, key)=><li key={`block-item-link-${key}`}>
    <Link to="example/link" className={block_details[1]}>
      <span>
        <span>
          <h4>Office Syndrome <img src={icon_check_star} alt="Office Syndrome" /></h4>
          <span>Health</span>
          <span>4.5M members</span>
        </span>
        <span className="recommended_blocks_img" style={{backgroundImage:`url("${trending_example}")`}}></span>
      </span>
    </Link>
  </li>);



  return (
    <section>
    
      <Container>
        <Row>
          <Col lg={12}>
            <div className="search_bar">
              <p>Discover our best supportive communities!</p>
              <InputGroup>
                <Form.Control
                  placeholder="Tell us what's your concern..."
                  aria-label="Recipient's username"
                  aria-describedby="basic-addon2"
                />
                <Button variant="success" id="button-addon2">
                  <FontAwesomeIcon icon={faSearch} />
                </Button>
              </InputGroup>
            </div>




            <div className="categories_boxes">
              <ul>
              {categories_boxes_links}
              </ul>
            </div>


            <div className="recommended_blocks">
              <h3>Recommended for you</h3>
              <div className="recommended_blocks_list">
                <ul>
                {recommended_blocks}
                </ul>
              </div>
            </div>



            <div className="recommended_blocks trending_blocks">
              <h3>Todays trending</h3>
              <ul>
              {trending_blocks}
              </ul>
            </div>
          </Col>
        </Row>
      </Container>



    </section>
  );
}