import {
  // BrowserRouter as Router,
  Route,
  Routes,
  // Link
} from 'react-router-dom';



import Feed from './../SupportGroups/Feed';
import HealthSupportGroups from './../SupportGroups/HealthSupportGroups';

import SubmenuTopLeft from './../../Components/SubmenuTopLeft';


export default function PageContent(){


  

  return (
    <div className="page_content">
      <SubmenuTopLeft />
      <Routes>
        {/* <Route exact path="/" element={<Home/>} />
        <Route exact path="/About" element={<About/>} />
        <Route exact path="/Contact" element={<Contact/>} /> */}
        <Route exact path="/" element={<Feed />} />
        <Route exact path="/SupportGroup/Feed" element={<Feed />} />
        <Route exact path="/SupportGroup/HealthSupportGroups" element={<HealthSupportGroups />} />
      </Routes>
    </div>
  );
}