import { Link } from "react-router-dom";
import React, { useState } from 'react';
import Menus from "../../Global/Menus";

export default function BottomMobileNavigation(){

  
  const [activeMenuPath, setactiveMenuPath] = useState('');


  const list = Menus.sidebar_left.map((details, key)=><li key={`item-mobile-botto-${key}`}>
    <Link to={details[2]} className={details[2]===activeMenuPath?'active':''} onClick={()=>{setactiveMenuPath(details[2]);}}>
      {details[0]}
    </Link>
  </li>);

  return (
    <div className="bottom_mobile_navigation">
      <ul>{list}</ul>
    </div>
  );
}