import {
  // BrowserRouter as Router,
  // Route,
  // Routes,
  Link
} from 'react-router-dom';

import example_profile_photo_feed from './../../../Assets/Images/example_profile_photo_feed.jpg';
import example_profile_photo_feed_sub from './../../../Assets/Images/example_profile_photo_feed_sub.jpg';
import example_feed_main_photo from '../../../Assets/Images/example_feed_main_photo.jpg';
import example_sidebar_image from '../../../Assets/Images/example_sidebar_image.jpg';
import icon_check_star from '../../../Assets/Images/icon-check-star-13x13.svg';


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPencilAlt, faEllipsisH } from '@fortawesome/fontawesome-free-solid';
import { faHeart, faComment } from '@fortawesome/fontawesome-free-regular';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


import Button from 'react-bootstrap/Button';

export default function Feed(){

  const feed_content_list = [1,2,3,4,5].map((content, key)=>
    <div className="feed_content_list_item" key={`feed_content_list_item-${key}`}>
      
      <div className="feed_content_list_item_person">
        <div className="photo" style={{backgroundImage:`url("${example_profile_photo_feed}")`}}>
          <div style={{backgroundImage:`url("${example_profile_photo_feed_sub}")`}}></div>
        </div>
        <div className="content">
          <h5>Lydia Press <img src={icon_check_star} alt="Lydia Press" /></h5>
          <ul>
            <li><Link to="">Facilitator</Link></li>
            <li className="divider"></li>
            <li><Link to="">Hospital Buddy</Link></li>
          </ul>
        </div>
      </div>

      <div className="feed_date_time">
        Jun 23, 2022 at 14:38
      </div>

      <p className="feed_paragraph">Hi folks! Our pretty Venus is back as healthy as ever! She cant wait to meet you all on the 2nd floor at Building A :)</p>
      <div className="feed_main_photo" style={{backgroundImage:`url("${example_feed_main_photo}")`}}></div>
      <ul className="feed_footer_buttons">
        <li>
          <Button variant="light">
            <FontAwesomeIcon icon={faHeart} />  237
          </Button>
        </li>
        <li>
          <Button variant="light">
            <FontAwesomeIcon icon={faComment} />  45
          </Button>
        </li>
        <li>
          <Button variant="light">
            <FontAwesomeIcon icon={faEllipsisH} />
          </Button>
        </li>
      </ul>

    </div>
  );


  const feed_content_sidebar_list = [1,2,3,4,5,6,7,8].map((details, key)=><li key={`list-item-${key}`}>
    <Link to="./">
      <span style={{backgroundImage:`url("${example_sidebar_image}")`}}></span>
      <h5>Community name</h5>
    </Link>
  </li>);

  return (
    <section className="feed_content">
      <Container>
        <Row>
          <Col lg={8}>
            <div className="feed_content_list">
            {feed_content_list}
            </div>
          </Col>
          <Col lg={4}>
            <div className="feed_content_sidebar">
              
              <div className="feed_content_sidebar_item">
                <h4>Lorem ipsum dolor sit amet </h4>
                <p>Consectetur adipiscing elit. Dui pretium iaculis mattis morbi. Amet.</p>
                <Button variant="success" size="lg" className="feed_sidebar_action_button">
                  <FontAwesomeIcon icon={faPencilAlt} /> New post
                </Button>
              </div>

              <div className="feed_content_sidebar_item">
                <h4>My support groups</h4>
                <ul className="feed_content_sidebar_list">
                  {feed_content_sidebar_list}
                </ul>
              </div>



              <Button variant="success" size="lg" className="feed_sidebar_action_button feed_sidebar_action_button_mobile">
                <FontAwesomeIcon icon={faPencilAlt} /> New post
              </Button>


            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
}