import {
  // BrowserRouter as Router,
  // Route,
  // Routes,
  Link,
} from 'react-router-dom';

import React, { useState } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCalendar, faStethoscope, faHandHoldingHeart, faCommentDots, faUser, faInfoCircle } from '@fortawesome/fontawesome-free-solid';

import profile_photo_sara from '../../Assets/Images/profile-photo-sara.jpg';

import Menus from '../../Global/Menus';

export default function SidebarLeft(){

  const [activeMenuPath, setactiveMenuPath] = useState('');


  const menu_list = 
  /*[
    [<FontAwesomeIcon icon={faCalendar} />, 'Appointment', 'Appointment'],
    [<FontAwesomeIcon icon={faStethoscope} />, 'Medical report', 'MedicalReport'],
    [<FontAwesomeIcon icon={faHandHoldingHeart} />, 'Support groups', 'SupportGroup/Feed'],
    [<FontAwesomeIcon icon={faCommentDots} />, 'Message', 'Message'],
    [<FontAwesomeIcon icon={faUser} />, 'Profile', 'Profile'],
  ]*/
  Menus.sidebar_left.map((link_details, key)=><li key={`sidebar-item-${key}`}>
    <Link to={link_details[2]} onClick={()=>{setactiveMenuPath(link_details[2])}} className={`btn btn-link ${link_details[2]===activeMenuPath?'active':''}`}>
      {link_details[0]} <span>{link_details[1]}</span>
    </Link>
  </li>);

  return (
    <div className="sidebar_left">
      <div className="siderbar_left_green">
        
        <div className="sidebar_left_top_content">
          <div className="sidebar_left_profile_photo" style={{backgroundImage:`url("${profile_photo_sara}")`}}>
            <img src={profile_photo_sara} alt="Sarah" />
          </div>
          <h1>Hi, Sarah</h1>
          <ul className="sidebar_left_menu">{menu_list}</ul>
        </div>

        <div className="sidebar_left_bottom_content">
          <FontAwesomeIcon icon={faInfoCircle} />
          <p>You have an appointment with us tommorow!</p>
          <Link to="not/defined/yet">View preparation checklist</Link>
        </div>

      </div>
    </div>
  );
}