import {
  // BrowserRouter as Router,
  // Route,
  // Routes,
  Link
} from 'react-router-dom';

import React, { useState } from 'react';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

export default function SubmenuTopLeft(){

  const [activeMenuPath, setactiveMenuPath] = useState('SupportGroup/Feed');
  //console.log('activeMenuPath:', activeMenuPath);

  const submenu = [
    ['Feed', 'SupportGroup/Feed'],
    ['Health Support Groups', 'SupportGroup/HealthSupportGroups']
  ].map((link_details, key)=><li key={`submenu-item-${key}`}>
    <Link to={link_details[1]} onClick={()=>{setactiveMenuPath(link_details[1])}} className={activeMenuPath===link_details[1]?'active':''}>
      {link_details[0]}
    </Link>
  </li>);

  return (
    <div className="submenu_top_left">
      <Container>
        <Row>
          <Col lg={12}>
            <ul>
              {submenu}
            </ul>
          </Col>
        </Row>
      </Container>
    </div>
  );
}