import {
  // BrowserRouter as Router,
  // Route,
  // Routes,
  Link
} from 'react-router-dom';


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faStethoscope, faCalendarCheck, faClipboardCheck } from '@fortawesome/fontawesome-free-solid';


export default function NavigationBottomGreen(){
  


  const navigation_items = [
    [<span><FontAwesomeIcon icon={faStethoscope} /><br />Find a doctor</span>, 'example/link'],
    'd',
    [<span><FontAwesomeIcon icon={faClipboardCheck} /><br />Inquiry</span>, 'example/link'],
    'd',
    [<span><FontAwesomeIcon icon={faCalendarCheck} /><br />Appointment</span>, 'example/link']
  ].map((details, key)=>
    details==='d'?
    <li key={`navigation-item-${key}`} className="divider"></li>
    :
    <li key={`navigation-item-${key}`}>
      <Link to={details[1]}>{details[0]}</Link>
    </li>
  );

  return (
    <div className="navigation_bottom_green">
      <ul>
      {navigation_items}
      </ul>
    </div>
  );
}